body {
  font-family: 'Segoe UI', sans-serif;
  background: #0e1621;
  color: #d3d6db;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.card {
  background: #1a2230;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  padding: 24px;
  overflow-x: auto;
}

/* Table */
.table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.85rem;
  color: #ffffff;
}

.table thead th {
  position: sticky;
  top: 0;
  background-color: #1f2a3c;
  color: #bfc8d4;
  text-align: left;
  font-weight: 600;
  padding: 12px;
  border-bottom: 1px solid #2e3b4e;
  z-index: 1;
}

.table td {
  padding: 12px;
  border-bottom: 1px solid #2e3b4e;
  vertical-align: middle;
}

.table tbody tr:hover {
  background-color: #2e3b4e;
}

/* Sticky headers */
.table th.clickable {
  transition: background-color 0.2s, color 0.2s;
}
.table th.clickable:hover {
  background-color: #324054;
  color: #ffffff;
  cursor: pointer;
}

/* Buttons */
button {
  background-color: #6200ee;
  border: none;
  color: white;
  padding: 6px 10px;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.75rem;
}
button:hover {
  background-color: #3700b3;
}

/* Trends */
.trend-green {
  background-color: #1e4620;
  color: #4caf50;
  font-weight: bold;
  border-radius: 12px;
  padding: 4px 8px;
  font-size: 0.75rem;
  display: inline-block;
}

.trend-red {
  background-color: #4a1c1c;
  color: #f44336;
  font-weight: bold;
  border-radius: 12px;
  padding: 4px 8px;
  font-size: 0.75rem;
  display: inline-block;
}

.trend-yellow {
  background-color: #514600;
  color: #ffc107;
  font-weight: bold;
  border-radius: 12px;
  padding: 4px 8px;
  font-size: 0.75rem;
  display: inline-block;
}

/* Player photo */
.photo {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  object-fit: cover;
}

/* Navbar */
.navbar {
  padding: 0.5rem;
  background: #1a2230;
  text-align: center;
}
.nav-button {
  display: inline-block;
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #6200ee;
  color: white;
  font-weight: 600;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.2s;
}
.nav-button:hover {
  background-color: #3700b3;
}
/* Highlight sortable column header on hover */
th.sortable:hover {
  background-color: #f0f0f0;
  transition: background 0.2s;
}